<template>
  <div>
    <top-nav title="库存管理"></top-nav>
    <div class="wyu-tips wyu-tips-warning">
      <p>1) 这里是库存管理</p>
      <p>2) 库存仅供参考，设置时请留有余量。</p>
    </div>
    <div class="wyu-main" v-loading="loading">
      <div class="wyu-block">
        <el-form label-width="80px" size="mini" v-model="searchForm" class="search-form" :inline="true">
          <el-form-item label="商品ID" label-width="100">
            <el-input v-model="searchForm.goods_id"></el-input>
          </el-form-item>
          <el-form-item label="商品名称" label-width="100">
            <el-input v-model="searchForm.goods_name"></el-input>
          </el-form-item>

          <el-form-item label="商品码" label-width="100">
            <el-input v-model="searchForm.goods_sn"></el-input>
          </el-form-item>

          <el-form-item label="规格码" label-width="100">
            <el-input v-model="searchForm.bar_code"></el-input>
          </el-form-item>

          <el-form-item label="库存状态" label-width="100">
            <el-select v-model="searchForm.stock_status" placeholder="请选择">
              <el-option :value=-999 :key=-999 label="请选择"></el-option>
              <el-option v-for="(item,k) in datas.stock_status_mean" :value=k :key=k :label=item></el-option>
            </el-select>
          </el-form-item>


          <div style="padding-left:80px;">
            <el-button icon="el-icon-search" @click="getGoodsStock(1)" size="mini" type="primary">搜索</el-button>
            <a href="javascript:void(0);" @click="searchForm.goods_name = '', searchForm.goods_id = '',
            searchForm.KEY = '', searchForm.bar_code = '', searchForm.stock_status = -999,getGoodsStock()">取消搜索</a>
          </div>
        </el-form>

        <el-table :data="datas.list" style="width: 100%" size="mini" @selection-change="handleSelectionChange">
          <el-table-column type="selection" width="55"></el-table-column>
          <el-table-column prop="goods_id" label="商品ID" width="70"></el-table-column>
          <el-table-column label="编码" width="200">
            <template slot-scope="scope">
              <p>商品码: {{scope.row.goods_sn}}</p>
              <p>规格码: {{scope.row.bar_code}}</p>
            </template>
          </el-table-column>
          <el-table-column label="商品名称" width="350" >
            <template slot-scope="scope">
              <div class="ellipsis-02">
                {{scope.row.goods_name}}
              </div>
            </template>
          </el-table-column>

          <el-table-column label="缩略图"  width="80">
            <template slot-scope="scope">
              <img :src="scope.row.photo" width="50" />
            </template>
          </el-table-column>

          <el-table-column label="规格">
            <template slot-scope="scope">
              <p v-if="scope.row.key_name.length < 0">无规格</p>
              <p v-if="scope.row.key_name.length > 0" v-for="item in scope.row.key_name">{{item}}</p>
            </template>
          </el-table-column>

          <el-table-column label="库存数量" width="100" prop="goods_num">
              <template slot-scope="scope" >
                <h1 v-if="scope.row.goods_num <= stockLimit.sold_out" style="color: #909399">{{scope.row.goods_num}}</h1>
                <h1 v-if="scope.row.goods_num > stockLimit.sold_out && scope.row.goods_num <= stockLimit.critical" style="color: #f56c6c">{{scope.row.goods_num}}</h1>
                <h1 v-if="scope.row.goods_num > stockLimit.critical && scope.row.goods_num <= stockLimit.danger" style="color: #e6a23c">{{scope.row.goods_num}}</h1>
                <h1 v-if="scope.row.goods_num > stockLimit.danger && scope.row.goods_num <= stockLimit.safe" style="color: #409EFF;">{{scope.row.goods_num}}</h1>
                <h1 v-if="scope.row.goods_num > stockLimit.safe" style="color: #67c23a">{{scope.row.goods_num}}</h1>
              </template>
          </el-table-column>

          <el-table-column label="库存预警" prop="goods_num" width="100">
            <template slot-scope="scope">
              <el-tag v-if="scope.row.goods_num <= stockLimit.sold_out" type="info">无货</el-tag>
              <el-tag v-if="scope.row.goods_num > stockLimit.sold_out && scope.row.goods_num <= stockLimit.critical" type="danger">即将售罄</el-tag>
              <el-tag v-if="scope.row.goods_num > stockLimit.critical && scope.row.goods_num <= stockLimit.danger" type="warning">库存紧张</el-tag>
              <el-tag v-if="scope.row.goods_num > stockLimit.danger && scope.row.goods_num <= stockLimit.safe" >需要关注</el-tag>
              <el-tag v-if="scope.row.goods_num > stockLimit.safe" type="success">库存充足</el-tag>
            </template>
          </el-table-column>

          <el-table-column label="操作信息"  width="200">
            <template slot-scope="scope" >
              <el-button type="text" size="mini" @click="inOut(scope),dialog=true" >库存操作</el-button>
            </template>
          </el-table-column>
        </el-table>
        <div class="page">
          <div class="page-tool" >

            <el-button size="mini" type="success" @click="dialog=true">批量操作库存</el-button>
          </div>
          <div class="page-mian">
            <el-pagination @current-change="getGoodsStock" :page-size="datas.limit"
              layout="total, prev, pager, next" :total="datas.count">
            </el-pagination>
          </div>
        </div>
      </div>
    </div>


    <!--编辑新增 弹窗-->
    <el-dialog title="库存操作"  v-loading="subLoading" :visible.sync="dialog" width="25%" :modal-append-to-body="false">
      <el-form :model="form" style="padding: 0" size="small">
        <el-form-item label="操作类型" label-width="80px">
          <el-select v-model="form.type" placeholder="请选择">
            <el-option :key="-999" label="请选择" :value=-999></el-option>
            <el-option v-for="(item,key) of manageType" :key="key" :label="item" :value="key"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="操作数量" label-width="80px">
          <el-input v-model="form.number" autocomplete="off" ></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click="dialog = false">取 消</el-button>
        <el-button size="small" type="primary" @click="changeNum(true)">确 定</el-button>
      </div>
    </el-dialog>
  </div>



</template>

<script>
    import TopNav from "@/components/TopNav";
    import * as api from "@/api";

    export default {
        name: 'commentIndex',
        components: {TopNav},
        data() {
            return {
                subLoading: false,
                dialog: false,
                //整体列表 菊花
                loading: false,
                //列表数据 包含 list count limit
                datas: [],
                //修改 添加 form
                //搜索 form
                searchForm: {
                    goods_id: '',
                    goods_name: '',
                    sku: '',
                    bar_code: '',  // 手机号码
                    stock_status: -999,
                },
                img: [],
                repertoryIds: [],
                repertory_id: 0,
                stockLimit: {
                    sold_out: 0,
                    critical: 10,
                    danger: 50,
                    safe: 100,
                },
                manageType: {

                },
                form: {
                    type: -999,
                    number: 0,
                },
            }
        },

        created() {
            this.getGoodsStock();
        },

        methods: {
            a(e) {
                console.log(e);
            },

            handleSelectionChange(val) {
                let repertoryIds = [];
                for (let i in val) {
                    repertoryIds.push(val[i].repertory_id)
                }
                this.repertoryIds = repertoryIds;
            },


            /**
             * 获取库存列表
             * @param page {number} 页数
             */
            getGoodsStock(page = 1) {
                this.loading = true;
                api.post("/shop/admin.goods.stock/stockList", {
                    appToken: this.$store.getters.appToken,
                    page: page,
                    goods_name: this.searchForm.goods_name,
                    goods_id: this.searchForm.goods_id,
                    sku: this.searchForm.sku,
                    bar_code: this.searchForm.bar_code,
                    goods_sn: this.searchForm.goods_sn,
                    stock_status: this.searchForm.stock_status,
                    limit: 20,
                }).then(res => {
                    this.datas = res;
                    this.stockLimit = res.status_limit;
                    this.manageType = res.manage_type;
                    this.loading = false;
                }).catch((error) => {
                    this.loading = false;
                });
            },


            //出入库操作
            inOut(scope) {
                this.repertory_id = scope.row.repertory_id;
            },

            //changeNum
            changeNum() {
                this.loading = true;
                api.post("/shop/admin.goods.stock/changeStock", {
                    appToken: this.$store.getters.appToken,
                    repertory_id: this.repertory_id,
                    type: this.form.type,
                    number: this.form.number,
                    repertoryIds: JSON.stringify(this.repertoryIds),
                }).then(res => {
                    this.$message({
                        message: '操作成功！',
                        type: 'success',
                    });
                    this.dialog = false;
                    this.loading = false;
                }).catch((error) => {
                    this.loading = false;
                });
            }

        }
    }
</script>

